import {
  Button,
  Card,
  CardActions,
  CardContent,
  CardHeader,
  makeStyles,
  Typography,
} from "@material-ui/core";
import { observer } from "mobx-react";
import React from "react";
import { ILendingNetworkGist } from "../../services/lendingNetworksService/ILendingNetworksService";
import { TokenIcon } from "../../components/icons/TokenIcon";
import {
  BACKGROUND_LIGHT_COLOR,
  TEXT_ON_LIGHT,
} from "../../theme/themeConstants";

interface IProps {
  networkGist: ILendingNetworkGist;
  onViewNetwork: (networkId: string) => void;
}

const useStyles = makeStyles((theme) => ({
  card: {
    margin: "0.5rem",
    display: "flex",
    flexDirection: "column",
    justifyContent: "space-between",
    alignItems: "center",
    flex: 1,
    maxWidth: "20rem",
    backgroundColor: BACKGROUND_LIGHT_COLOR,
    borderRadius: "10%",
    // backgroundColor: "rgba(0,0,0,0.1)",
  },
  title: {
    textAlign: "center",
    fontWeight: "bold",
    color: TEXT_ON_LIGHT,
  },
  content: {
    display: "flex",
    flexWrap: "wrap",
    justifyContent: "center",
    color: TEXT_ON_LIGHT,
  },
  lnAsset: {
    margin: "0.1rem",
  },
}));

export const NetworkListItem: React.FC<IProps> = observer((props) => {
  const classes = useStyles();
  const { onViewNetwork, networkGist } = props;
  const { id, name, assets, distributionAssetParameters } = networkGist;
  const { symbol } = distributionAssetParameters;
  // TODO : Fix styling
  return (
    <Card className={classes.card}>
      <CardHeader title={name} className={classes.title} />
      <Typography variant={"body1"} style={{ fontWeight: "bold" }}>
        In alpha
      </Typography>
      {/*<Typography variant={"body2"}>Please try with small amounts</Typography>*/}
      <CardContent>
        {/*{assets.map((a) => (*/}
        {/*  <div key={a.symbol}>{a.name}</div>*/}
        {/*))}*/}
        {assets.map((a) => (
          <TokenIcon
            symbol={a.symbol}
            key={a.symbol}
            extraClasses={[classes.lnAsset]}
          />
        ))}
      </CardContent>
      <CardActions
        style={{
          display: "flex",
          justifyContent: "center",
        }}
      >
        <Button
          variant="contained"
          color="primary"
          onClick={() => onViewNetwork(id)}
        >
          View Network
        </Button>
      </CardActions>
    </Card>
  );
});
